body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
h1 {
  color: red;
  font-style: italic;
}
h2 {
  color: red;
}
button {
  margin-left: 5px;
  border: solid 1px;
}
li {
  color: grey;
